import React from 'react'
import styled from 'styled-components'
import ImageGallery from 'react-image-gallery'

export default function Portfolio(props){
    var portfolioHeight = props.height.slice(0,(props.height).length-2)
    var imgWidth = props.imgWidth.slice(0,(props.imgWidth).length-2)

    const Portfolio = styled.div`
        position: relative;
        z-index: 1;
        border: 1px solid;
        border-color: #00000015;
        border-radius: 0.125vw;
        margin: 1vw;
        padding: 1vw;
        float: left;
        width: 25vw;
        height: ${portfolioHeight+"vw"};
        padding-right: 2vw;
        background-color: #FFFFFF;
        filter: drop-shadow(0px 12px 10px #00000015);
        transition: all ease-in-out 0.15s;
        &:hover{
            transform: scale(1.025);
            transition: all ease-in-out 0.25s;
            filter: drop-shadow(2px 10px 5px #00000015);
        }
        @media screen and (max-width: 600px) {
            float: none;
            width: 80vw;
            height: ${portfolioHeight*3.25 + "vw"};
            margin-bottom: 4vw;
            
        }
    `
    const Title = styled.h1`
        font-size: 1.5vw;
        margin-left: 1vw;
        opacity: 70%;
        @media screen and (max-width: 600px) {
            font-size: 5vw;
        }
    `
    const Description = styled.p`
        font-size: 1vw;
        margin-left: 1vw;
        opacity: 30%;
        margin-top: -1vw;
        text-align: justify;
        @media screen and (max-width: 600px) {
            margin-top: -2vw;
            font-size: 3vw;
        }
    `
    const Img = styled.img` 
        width: ${imgWidth+"vw"};
        @media screen and (max-width: 600px) {
            margin: 1vw;
            width: ${imgWidth*3 + "vw"};
        }

    `
    return(
        <Portfolio>
            <Img src={props.img} />
            <Title>{props.title}</Title>
            <Description>{props.description}</Description>
        </Portfolio>
    )
}