import React from "react"
import styled from 'styled-components'

function Footer(){
    const Icon = styled.img` 
        float: left;
        margin: 0.35vw;
        width: 0.75vw;
        @media screen and (max-width: 600px) {
            margin-top: 0.75vw;
            width: 2vw;
        }
    `
    const Link = styled.div`
        margin-left: 7vw;
        transition: ease-in-out 0.2s;
        opacity: 35%;
        &:hover{
            transform: scale(1.025);
            transition: ease-in-out 0.2s;
            cursor: pointer;
            opacity: 100%;
        }
        @media screen and (max-width: 600px) {
            margin-left: 23.5vw;
        }
    `
    return (
        <div className="footersection">
            <h1 style={{lineHeight: "0%", opacity: ".35", fontWeight: "lighter", marginTop: "5%", textAlign: "center"}}>Designed and developed by Mahad Rehan © Mahad Rehan 2021</h1>
            <a href="https://github.com/mahaaad" target="_blank">
            <Link>
                <Icon src="/images/icons/github.png"  />
                <h1 style={{color: "black", margin: "0.25vw", float: "left"}}>GitHub</h1>
            </Link>
            </a>
            <a href="https://linkedin.com/in/mahadrehan/" target="_blank">
            <Link>
                <Icon src="/images/icons/blacklinkedin.svg" />
                <h1 style={{color: "black", margin: "0.25vw", float: "left"}}>LinkedIn</h1>
            </Link>
            </a>
        </div>
    )
}

export default Footer