import React from "react"
import styled from 'styled-components'
import ReactTooltip from 'react-tooltip';


function Project(props){

const Project = styled.div`
    width: 60vw;
    height: 22.5vw;
    transition: all ease-in-out 0.15s;
    filter: drop-shadow(2px 10px 5px #00000015);
    &:hover{
        transform: translate(0,-10px);
        transition: all ease-in-out 0.25s;
        filter: drop-shadow(2px 10px 8px #00000025);
    }
    @media screen and (max-width: 600px) {
        width: 90vw;
        height: 90vw;
        &:hover{
            transform: translate(0,-10px);
            transition: all ease-in-out 0.25s;
            filter: drop-shadow(2px 10px 8px #00000025);
        }
    }
`
const Thumbnail = styled.img`
    width: 35vw;
    position: relative;
    z-index: 1;
    float: ${props.float == "left" ? "right" : "left"};
    @media screen and (max-width: 600px) {
        float: left;
        width: 85vw;
    }
`
const Title = styled.h1` 
    font-size: 1.5vw;
    opacity: 80%;
    float: right;
    margin-right: 2vw;
    padding-top: 2.5vw;
    @media screen and (max-width: 600px) {
        position: relative;
        float: left;
        font-size: 4vw;
        margin-right: 0vw;
        margin-top: -55vw;
        z-index: 2;
        color: black;
        width: 50vw;
    }
`
const Tech = styled.h1`
    font-size: 0.75vw;
    opacity: 80%;
    float: ${props.float};
    margin-left: 0.75vw;
    margin-right: 0.75vw;
    @media screen and (max-width: 600px) {
        float: left;
        font-size: 2.5vw;
    }

`
const Description = styled.p`
    text-align: justify;
    font-size: 0.75vw;
    position: relative;
    z-index: 2;
    width: 22.5vw;
    height: 6vw;
    float: right;
    color: white;
    font-weight: 300;
    padding-top: 1vw;
    padding-left: ${props.float == "left" ? '1vw' : '3vw'};
    padding-right: ${props.float == "right" ? '1vw' : '3vw'};
    background-color: rgba(0, 0, 0, .7);
    border-radius: 2px;
    filter: drop-shadow(2px 10px 5px #00000030);
    -webkit-backdrop-filter: blur(4px);
    backdrop-filter: blur(4px);
    @media screen and (max-width: 600px) {
        float: left;
        width: 75vw;
        height: 15vw;
        font-size: 2.75vw;
        padding: 2vw;
        margin-left: -4vw;
        margin-top: -2vw;
        padding-bottom: 3vw;
    }
`
const Icon = styled.img` 
    opacity: 75%;
    width: 1vw;
    float: ${props.float};
    margin-left: 0.5vw;
    margin-right: 0.5vw;
    transition: ease-in-out 0.2s;
    &:hover{
        transform: translate(0,-5px);
        transition: ease-in-out 0.2s;
        opacity: 100%;
    }
    @media screen and (max-width: 600px) {
        width: 4vw;
        margin: 1vw;
    }
`
const InfoDiv = styled.div` 
    width: 25vw;
    height: 10vw;
    float: ${props.float};
    @media screen and (max-width: 600px) {
        width: 40vw;
        height: 20w;
        float: left;
    }
`
const TechDiv = styled.div` 
    width: 25vw;
    height: 1.75vw;
    float: ${props.float};
    @media screen and (max-width: 600px) {
        width: 65vw;
        height: 10w;
        float: left;
    }
`
const IconDiv = styled.div` 
    margin-top: 0.5vw;
    width: 10vw;
    height: 1.5vw;
    float: ${props.float};
    @media screen and (max-width: 600px) {
        margin-top: 2vw;
        width: 15vw;
        height: 10w;
        float: left;
    }
`
 return(
     <Project >
        <Thumbnail src={props.imgUrl} />
        <InfoDiv>
            <Title>{props.projectName}</Title>
            <Description>{props.description}</Description>
            <TechDiv>
                <Tech>{props.tech4}</Tech>
                <Tech>{props.tech3}</Tech>
                <Tech>{props.tech2}</Tech>
                <Tech>{props.tech1}</Tech>
            </TechDiv>
            <IconDiv>
                <a>
                    <Icon  src="/images/icons/link.png" />
                    <ReactTooltip />
                </a>
                <a href={props.githublink} target="_blank">
                    <Icon src="/images/icons/github.png" />
                    <ReactTooltip />
                </a>
            </IconDiv>
        </InfoDiv>
     </Project>
 )   
}

export default Project