import React from "react"

function SectionTitle(props){
    return(
        <div className= "sectiontitle">
            <hr style={{marginLeft: "3vw", opacity: "0.3"}} />
            <h2>{props.title}</h2>
            <img src = {props.icon}/>
            <hr style={{marginRight: "3vw", opacity: "0.3"}}/>
        </div>
    )
}

export default SectionTitle